const initialState = {
	tags : []
}



const tasksReducer = (state = initialState, action) => {
	switch(action.type){
		case 'REMOVE_TAG' :
			var updatedState = state
			updatedState.list.tags.splice(action.target, 1)
			return object.assign({},updatedState)

			break;

		default :
			return state
	}
}

export default tagsReducer



